import './Template.css';
import React from 'react';

function Template ({output, setOutput, tops, mids, bots}) {
   return (
      <div className='template'>
         <div className="headline">
            <h1>Template</h1>
            <p>Wählen Sie Ihre Präferenzen aus.</p>
         </div> <br /> <br />
         <div className='containerMenu'>
            <div className="headline2">
               <h2>Navigation</h2>
               <p>befindet sich am obersten Bildschirmrand für die Navigation der Seite</p>
            </div>
            <div className='selectMenu selectForm'>{tops.map((e) => (
               <button key={tops.indexOf(e)} name='top' onClick={(event) => {
                  setOutput ((prevState) => {
                      return {...prevState, ['top']: tops.indexOf(e)};
                  });
               }}>{e}</button>
            ))}</div>
         </div>  <br />
         <div className='containerMenu'>
            <div className="headline2">
            <h2>Vorlagen</h2>
            <p>Fertig erstellte Designs zum Auswählen</p>
            </div>
             <div className='selectMain selectForm'>{mids.map((e) => (
               <button key={mids.indexOf(e)} onClick={() => {
                  setOutput ((prevState) => {
                      return {...prevState, ['middle']: mids.indexOf(e)}
                  });
               }}>{e}</button>
            ))} </div> 
         </div> <br />
         <div className='containerMenu'>
            <div className="headline2">
            <h2>Vorlagen</h2>
            <p>Fertig erstellte Designs zum Auswählen</p>
            </div>
             <div className='selectFooter selectForm'>{bots.map((e) => (
               <button key={bots.indexOf(e)} onClick={() => {
                  setOutput ((prevState) => {
                      return {...prevState, ['bottom']: bots.indexOf(e)}
                  });
               }}>{e}</button>
            ))} </div> 
         </div>
      </div>
   );
}

export default Template;