import './Menubar.css';
import React from 'react';
import Menuitem from './Menuitem';

function Menubar ({setTab}) {
    const menutabs = ["Subscription", "Template", "Kontaktinfos", "Bestätigung"];

   return (
      <div className='flex-down'>
        {menutabs.map((e) => (
            <button className='inactive' key={menutabs.indexOf(e)} onClick={() => {
                setTab(e);
            }}><Menuitem label={e} /></button>
        ))}
      </div>
   );
}

export default Menubar;