import './ContentManager.css';
import React, { useEffect, useState } from 'react';
import Kontakt from './Kontakt';
import Subscription from './Subscription';
import Template from './Template';
import Confirmation from './Confirmation';
import Top1 from './Components/Top/Variant1';
import Top2 from './Components/Top/Variant2';
import Top3 from './Components/Top/Variant3';
import Mid1 from './Components/Middle/Variant1';
import Mid2 from './Components/Middle/Variant2';
import Mid3 from './Components/Middle/Variant3';
import Bot1 from './Components/Bottom/Variant1';
import Bot2 from './Components/Bottom/Variant2';
import Bot3 from './Components/Bottom/Variant3';
import axios from 'axios';

const StartOutput = {
   subscription: "template",
   top: 0,
   middle: 0,
   bottom: 0,
   vorname: "",
   nachname: "",
   email: ""
}

const tops = [<Top1 />,<Top2 />,<Top3 />];
const mids = [<Mid1 />,<Mid2 />,<Mid3 />];
const bots = [<Bot1 />,<Bot2 />,<Bot3 />];

function ContentManager ({current}) {
   const [error, setError] = useState();
   const [output, setOutput] = useState(StartOutput);
   const [send, setSend] = useState();
   
   useEffect(() => {
      setSend(output?.vorname+output?.nachname+"|"+output?.email+"|"+output?.subscription+"|"+(output?.top+1)+"|"+(output?.middle+1)+"|"+(output?.bottom+1));
      console.log(send);
   }, [output]);

   function handleSubmit (event) {
       event.preventDefault();
       (async () => {
           try {
               if (true) {
                   await axios.put(`${process.env.REACT_APP_SERVER_URL}/submit`,send);
                   setOutput(StartOutput);
               }else {
                   throw new Error("Ungültige Angaben");
               }
           } catch (error) {
               setError(error);
           }
       })();
   }

   if (current === "Subscription") return (
      <div className='form-content'>
         <Subscription />
      </div>
   );
   if (current === "Template") return (
      <div className='form-content'>
         <Template  output={output} setOutput={setOutput} tops={tops} mids={mids} bots={bots}/>
      </div>
   );
   if (current === "Kontaktinfos") return (
      <div className='form-content'>
         <Kontakt output={output} setOutput={setOutput}/>
      </div>
   );
   if (current === "Bestätigung") return (
      <div className='form-content'>
         <Confirmation top={tops[output.top]} middle={mids[output.middle]} bottom={bots[output.bottom]}/>
      </div>
   );
}

export default ContentManager;